import React from 'react';

type BookProps = {
    title: string,
    author: string,
    pageNum: number,
    coverImg: string,
}

const Book = (props: BookProps) => {

    return(
        <tr>
            <td><img src={props.coverImg} /></td>
            <td>{props.title}</td>
            <td>{props.author}</td>
            <td>{props.pageNum}</td>
        </tr>
    );
}

export default Book;