import axios from 'axios';
import * as cheerio from 'cheerio';
import constants from './constants';
const pretty = require('pretty');

const utils = {
    fetchShelfData: async (url: string) => {
        try {
            const { data } = await axios.get(`${constants.CORS_PROXY}${url}`);
            const $ = cheerio.load(data);
            const booksList = $('#booksBody').find('tr');
            const books = [];
            for(let i = 0; i < booksList.length; i++) {
                const bookData = $(booksList[i]);
                const title = bookData.find('.title .value a').text();
                const author = bookData.find('.author .value a').text();
                const pageNum = parseInt(bookData.find('.num_pages .value nobr').text());
                const coverImg = bookData.find('.cover .value img').prop('src');
                
                const book = {
                    title,
                    author,
                    pageNum,
                    coverImg
                }

                books.push(book);
            }

            return books;
        } catch (err) {
            console.error(err);
            return [];
        }
    }
}

export default utils;