import React, {useState, useEffect } from 'react';
import Book from './Book';
import utils from './utils';
import constants from './constants';

const BookTable = () => {
    const [books, setBooks]: any[] = useState([]);

    useEffect(() => {
        async function getBooks() {
            const books = await utils.fetchShelfData(constants.MY_GOODREADS_SHELF);
            setBooks(books);
        }

        getBooks();
    }, [])

    const totalPages = books.reduce((a: any, b: any) => a + b.pageNum, 0);
    return (
        <div>
            <p>Total pages: {totalPages}</p>
            <table>
                <tr>
                    <th></th>
                    <th>Title</th>
                    <th>Author</th>
                    <th>Pages</th>
                </tr>
            {
                books.map((book: any, index: any) => {
                    console.log(book);
                    return (
                        <Book title={book.title} author={book.author} pageNum={book.pageNum} coverImg={book.coverImg}/>
                    )
                })
            }
            </table>
        </div>
    )
}

export default BookTable;